import { MkDocsSearchIndex } from "../mkdocs"

/* ----------------------------------------------------------------------------
 * Types
 * ------------------------------------------------------------------------- */

/**
 * Search message type
 */
export const enum SearchMessageType {
  INIT,                                /* Initialize search index */
  LOAD,                                /* Load serialized search index */
  DUMP,                                /* Dump serialized search index */
  QUERY                                /* Query search index */
}

/* ------------------------------------------------------------------------- */

/**
 * Initialize search index
 */
export interface SearchInitMessage {
  type: SearchMessageType.INIT         /* Message type */
  data: MkDocsSearchIndex              /* Message data */
}

/**
 * Load serialized search index
 */
export interface SearchLoadMessage {
  type: SearchMessageType.LOAD         /* Message type */
  data: string                         /* Message data */
}

/**
 * Dump serialized search index
 */
export interface SearchDumpMessage {
  type: SearchMessageType.DUMP         /* Message type */
  data: string                         /* Message data */
}

/**
 * TODO: document
 */
export interface SearchQueryMessage {
  type: SearchMessageType.QUERY        /* Message type */
  data: string                         /* Message data */
}

/* ------------------------------------------------------------------------- */

/**
 * Search message
 */
export type SearchMessage =
  | SearchInitMessage
  | SearchLoadMessage
  | SearchDumpMessage
  | SearchQueryMessage
